@import "../scss/variables";
@import "main-panel";


div#frontis-blocks-sidebar\:frontis-blocks-sidebar {
	.components-panel__header {
		&.interface-complementary-area-header {
			min-height: 58px;
			background: $white-color;
			padding: 14px 16px;

			.interface-complementary-area-header__title {
				color: $neutral-color-950;
				font-family: $heading-font-family;
				font-size: 14px;
				font-weight: 600;
				letter-spacing: 0.00px;
				line-height: 19px;
			}
		}
	}

	.fb-global-back {
		padding-left: 0;
		gap: 10px;
		display: flex;
		align-items: center;

		span {
			svg {
				fill: none !important;
			}
		}
	}

	.fb-setting-spacing-inner-container {
		padding: 0 10px;
	}

	.fb-setting-spacing-label-container {
		margin: 14px -7px !important;
	}

	.fb-global-color-panel .components-tools-panel-header {
		padding-bottom: 0;
	}

	.fb-custom-color-panel {
		// border-right: 1px solid rgb(221, 221, 221);
		// border-left: 1px solid rgb(221, 221, 221);
		margin-top: 24px;
		border-top: none;
		position: relative;

		.components-tools-panel-header {
			padding-bottom: 0;
			margin-bottom: 12px;
		}

		.components-text {
			font-family: $heading-font-family;
			font-size: $medium-font-size-global;
			font-weight: 500;
			line-height: 16px;
		}

		.add-custom-color {
			position: absolute;
			top: 0;
			right: 26px;
			padding: 0;

			.add-custom-color-btn {
				padding: 0;
				background: none;
				height: unset;
				border-radius: none;
				color: #404655;
				font-size: unset;
				line-height: 0;
				align-items: center;
				gap: unset;

				svg {
					path {
						stroke: #404655;
					}
				}
			}
		}
	}

	.fb-gradient-color-panel {
		border-top: 1px solid rgb(221, 221, 221);
		border-right: 1px solid rgb(221, 221, 221);
		border-left: 1px solid rgb(221, 221, 221);
		border-radius: 4px;

		&:nth-child(3) {
			margin-top: 24px;
		}
	}

	.fb-blocks-color-tabs {
		padding: 0 16px;

		.components-tab-panel__tabs {
			padding-left: 16px;
			border-top: 1px solid rgb(221, 221, 221);
			border-left: 1px solid rgb(221, 221, 221);
			border-right: 1px solid rgb(221, 221, 221);

			button {
				font-size: $medium-font-size-global;
				font-weight: 500;
				line-height: 16px;
				border-bottom: 2px solid transparent;

				&.active-tab {
					// border-bottom: 2px solid $neutral-color-200;
				}

				&:first-child {
					padding-left: 0;
				}
			}
		}

		.components-tab-panel__tab-content {
			.components-tools-panel {
				.components-flex {
					&.components-h-stack {
						padding-top: 16px;
						padding-left: 12px;
						padding-right: 3px;

						button {
							svg {
								fill: $neutral-color-600;
							}
						}
					}
				}

				.components-tools-panel-item {
					border-left: 0;
					border-right: 0;
					margin-top: 0;

					.components-button {
						border-right: 0;
						border-left: 0;
						//padding: 14px 12px;
						padding: 0 12px;
						height: 48px;

						.components-flex-item {
							color: $neutral-color-700;
							font-family: $body-font-family;
							font-weight: 500;
							font-size: 14px;
							line-height: 16px;
						}
					}

					.components-flex {
						&.components-h-stack {
							padding-top: 0;
							padding-left: 0;
						}
					}
				}

				&.block-editor-panel-color-gradient-settings {
					border-top: none;
				}
			}
		}

		.add-custom-color {
			text-align: center;
			padding: 14px 0;

			.add-custom-color-btn {
				padding: 12px 14px;
				background: $primary-color;
				height: 44px;
				border-radius: 6px;
				font-family: $body-font-family;
				font-weight: 500;
				color: $white-color;
				font-size: 14px;
				line-height: 16px;
				align-items: center;
				gap: 10px;

				span {
					height: 100%;
					display: flex;
					align-items: center;
				}
			}
		}
	}

}

.block-editor-tools-panel-color-gradient-settings__dropdown>button.is-open {
	background: transparent;
}

.components-grid.components-tools-panel.block-editor-panel-color-gradient-settings.fb-color-panel.fb-global-color-panel {
	padding: 0;
	border: 1px solid $neutral-color-100;
	border-radius: 4px;

	.block-editor-tools-panel-color-gradient-settings__item {
		border: none;
		border-bottom: 1px solid $neutral-color-100;

		&:first-child {
			margin-top: 0;
		}

		&:last-child {
			border-bottom: none;
		}
	}
}

.components-grid.components-tools-panel.block-editor-panel-color-gradient-settings.fb-color-panel.fb-custom-color-panel {
	padding: 0 !important;

	.block-editor-tools-panel-color-gradient-settings__item {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding-right: 12px;
		margin-top: 0;

		&:first-child {
			border-radius: 4px 4px 0 0;
		}

		&:last-child {
			border-radius: 0 0 4px 4px;
		}

		.fb-delete-custom-color {
			display: none;
			border: none;
			background: transparent;
			color: $error-color;
			border-left: 1px solid #E1E3E8;
			padding-left: 12px;
			cursor: pointer;

			&:first-of-type {
				display: block;
			}
		}
	}
}

.fb-custom-color-item .actions {
	cursor: pointer;
	margin-right: 0;
	margin-left: auto;

	.dashicons-trash:before {
		color: $error-color;
	}
}

// Gradient color
.fb-color-panel.fb-gradient-color-panel {
	.components-panel__row.fb-gradient-color-label {
		color: $neutral-color-950;
		font-family: $heading-font-family;
		font-size: 14px;
		font-weight: 600;
		letter-spacing: 0.00px;
		line-height: 19.2px;
		padding: 14px 12px;
		border-bottom: 1px solid $neutral-color-100;
		display: none;
	}

	.fb-gradient-color-list {
		.fb-custom-color-item {
			display: flex;
			align-items: center;
			justify-content: space-between;
			height: 44px;
			padding: 0 12px;
			border-bottom: 1px solid $neutral-color-100;

			.item-content {
				display: flex;
				align-items: center;
				gap: 8px;
				color: $neutral-color-700;
				font-family: $body-font-family;
				font-weight: 500;
				font-size: 14px;
				line-height: 16px;
			}

			&:last-child {
				//border-bottom: none;
			}

			.actions {
				.fb-reset {
					.dashicons-image-rotate {
						cursor: pointer;

						&:before {
							color: $neutral-color-700;
							font-size: 17px;
						}
					}
				}
			}
		}
	}
}

#tab-panel-1-gradient-view {
	.fb-gradient-color-panel {
		.fb-gradient-color-label {}
	}
}

.fb-gradient-color-popup {
	.fb-gradient-color-popup-content {
		padding: 10px;
	}

	.components-circular-option-picker {
		.components-circular-option-picker__swatches {
			display: none !important;
		}
	}
}

.fb-global-typo-elements-wrap {
	// background: $neutral-color-50;

	.components-navigator-back-button {
		&:focus {
			outline: none !important;
			box-shadow: none !important;
		}
	}

	.fb-custom-element {
		border-bottom: 1px solid $neutral-color-100;
		padding: 14px 12px;

		&:first-child {
			border-top: 1px solid $neutral-color-100;
			border-radius: 4px 4px 0 0;
		}

		&:last-child {
			border-radius: 0 0 4px 4px;
		}

		button {
			padding: 0;
			width: 100%;
			gap: 10px;
			font-size: 14px;
			font-weight: 400;
			color: $neutral-color-700;
			height: auto;

			&:focus {
				outline: none !important;
				box-shadow: none !important;
			}

			.font_preview {
				// background: $neutral-color-100;
				// padding: 6px;
				border-radius: 2px;
				font-size: 12px;
				// min-height: 26px;
				display: flex;
				align-items: center;
				justify-content: center;
			}
		}

		&:last-child {
			.font_preview {
				background: $primary-color-base;

				svg {
					path {
						fill: $white-color;
					}
				}
			}
		}

		&.fb-global-typography-element {
			display: flex;
			align-items: center;
			justify-content: space-between;
			border-left: 1px solid $neutral-color-100;
			border-right: 1px solid $neutral-color-100;
			height: 44px;

			&:hover {
				.components-button {
					color: $primary-color;

					.font_preview {
						svg {
							fill: $primary-color;
						}
					}
				}
			}

			&:nth-child(2) {
				.components-button {
					.font_preview {
						svg {
							fill: none;
							color: $primary-color;
						}
					}
				}
			}
		}

		.fb-delete {
			svg {
				fill: none;
			}
		}
	}

	.fb-custom-element-wrapper {
		border: 1px solid $neutral-color-100;
		border-radius: 4px;

		.fb-custom-element {
			padding: 12px 20px 12px 12px;

			&:first-child {
				border: none;
			}

			&:last-child {
				border-bottom: none;
				border-radius: none;
			}

			&.fb-global-typography-element {
				display: flex;
				align-items: center;
				justify-content: space-between;
				border: none;
				border-bottom: 1px solid $neutral-color-100;

				&:last-child {
					border-bottom: none;
					border-radius: none;
				}
			}

			.fb-delete {
				border-left: 1px solid $neutral-color-100;
				padding-left: 12px;
			}
		}
	}

	.components-navigator-screen {
		//padding-top: 15px;
	}

	.fb-typography-settings {
		.components-base-control {
			width: auto !important;
			margin-left: 0 !important;
		}

		.fb-typography-settings {
			.fb-letter-case-control {
				margin-top: 18px;
			}
		}
	}

	.fb-delete-custom-typo {
		margin-left: auto;
		margin-right: 0;
		width: 20px !important;

		.dashicons-trash {
			&:before {
				color: $error-color;
			}
		}
	}

	.add-custom-color {
		text-align: center;
		// padding: 14px 0;

		.add-custom-color-btn {
			padding: 12px 14px;
			background: $primary-color;
			height: 44px;
			border-radius: 6px;
			font-family: $body-font-family;
			font-weight: 500;
			color: $white-color;
			font-size: 14px;
			line-height: 16px;
			align-items: center;
			gap: 10px;

			span {
				height: 100%;
				display: flex;
				align-items: center;
			}
		}
	}
}

svg#fb-global-panel-icon {
	height: 25px;
}

.block-editor-panel-color-gradient-settings__color-name {
	font-family: $heading-font-family;
	font-size: $base-font-size;
	font-weight: 500;
	line-height: 16px;
}

.fb-delete {
	border-left: 1px solid $neutral-color-100;
	padding-left: 12px;
}